.top-bar{
    background-color: #ffffff;
    line-height: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
}
.top-bar .space{
    -ms-flex:1 1;
        flex:1 1;
}

.top-bar .hamburger-menu{
    -ms-flex-preferred-size: 18px;
        flex-basis: 18px;
 }
.top-bar .hamburger-menu img{
    height: 18px;
    -webkit-transition: 0.5s ease;
    -o-transition: 0.5s ease;
    transition: 0.5s ease;
    cursor: pointer;
    margin:auto;
    padding-left: 10px;
}
.top-bar .menu-hided img{
-webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
        transform: rotate(180deg);
    -webkit-transition: 0.5s ease;
    -o-transition: 0.5s ease;
    transition: 0.5s ease;
}
.top-bar .icon-search{
    -ms-flex-preferred-size: 18px;
        flex-basis: 18px;
    text-align: center;
}
.top-bar .icon-search img{
    margin:auto;
padding: 0 0  0 20px ;
height: 18px;
}
.top-bar .notification-icon{
    -ms-flex-preferred-size: 50px;
        flex-basis: 50px;
}
.top-bar .notification-icon img {
    padding: 20px 10px 0px ;
    width: 20px;
}
.top-bar .messeges-icon {
-ms-flex-preferred-size: 50px;
    flex-basis: 50px;
}
.top-bar .messeges-icon img {
    padding: 20px 10px 0px ;
    height: 17px;
}
.top-bar .avatar-icon{
-ms-flex-preferred-size: 70px;
    flex-basis: 70px;
}
.top-bar .avatar-icon .polygon{
    padding-right: 10px;
    padding-left: 5px;
    padding-bottom: 10px;
}
.top-bar .avatar-icon .photo{
    height: 35px;
    padding-top: 10px;
}
.top-bar .add-project-btn {
-ms-flex-preferred-size: 50px;
    flex-basis: 50px;
}
.top-bar .add-project-btn input{
    background-color: #5584ff;
    border-radius: 25px;
    padding:5px 15px;
    color:white;
    border: none;
    margin: 15px 15px 5px 0px;
}


